  width: 70px;
  height: 70px;
  color: #fff;
  opacity: .6;
  cursor: pointer;
  background-color: #ff6b01;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  transition: all .35s ease-in-out;
  animation: 2s ease-in-out infinite drag;
  display: flex;
  position: fixed;
  bottom: 5%;
  right: 5%;
  box-shadow: 2px 1px 3px rgba(0, 0, 0, .3);
}

