@media screen and (max-width: 767px) {
    .container {
        max-width: 100%;
        margin: 0 auto;
        padding: 0 20px;

    }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
    .container {
        max-width: 100%;
        margin: 0 auto;
        padding: 0 20px;
    }
}

@media screen and (min-width: 1200px) {
    .container {
        max-width: 1200px;
        margin: 0 auto;
        padding: 0 20px;
    }
}